/** 通用颜色设置 **/
.m-header {
  height: 6rem;
  line-height: 6rem;
  background-color: rgba(255, 255, 255, 0.8);
  border-bottom: 0.1rem solid #ccc;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}
.m-header .header-content {
  width: 114rem;
  margin: 0 auto;
}
.m-header .m-logo {
  margin: 1.4rem 0rem;
}
.m-header .m-dropdown,
.m-header .m-menu {
  float: right;
}



